export default function Typewriter(root, delay = 0) {
  if (!root) return;
  const words = root.dataset.typewriter.split(";");
  const writer = document.createElement("span");
  const wordsLength = words.length;
  let isDeleting = false;
  let currentIndex = 0;
  let displayText = "";
  let currentWord;

  writer.textContent = root.textContent;
  root.innerHTML = "";
  root.appendChild(writer);

  function tick() {
    let delta = 200 - Math.random() * 100;

    currentWord = currentIndex % wordsLength;

    if (isDeleting) {
      displayText = words[currentWord].substring(0, displayText.length - 1);
    } else {
      displayText = words[currentWord].substring(0, displayText.length + 1);
    }

    writer.innerHTML = displayText;

    if (isDeleting) {
      delta /= 2;
    }

    if (!isDeleting && displayText === words[currentWord]) {
      delta = 1000;
      isDeleting = true;
    } else if (isDeleting && displayText === "") {
      isDeleting = false;
      currentIndex++;
      delta = 500;
    }

    setTimeout(tick, delta);
  }

  setTimeout(tick, delay);

  document.head.appendChild(document.createElement("style")).textContent = `
    [data-typewriter] > span {
      border-right: 0.08em solid;
      padding-right: 0.05em;
      animation: blinkingCaret .8s steps(44) infinite normal;
    }
    @keyframes blinkingCaret {
      from {
        border-right-color: rgba(60,60,60, 1);
      }
      to{
        border-right-color: rgba(60,60,60, 0);
      }
    }
  `;
}
